<template>
    <app-history></app-history>
    <app-about></app-about>
    <app-services></app-services>
    <app-why></app-why>
    <app-stages></app-stages>
    <app-examples></app-examples>
    <app-beginning></app-beginning>
    <app-reviews></app-reviews>
    <app-useful></app-useful>  
</template>

<script>
import AppHistory from '../components/AppHistory';
import AppAbout from '../components/AppAbout';
import AppServices from '../components/AppServices';
import AppWhy from '../components/AppWhy';
import AppStages from '../components/AppStages';
import AppExamples from '../components/AppExamples';
import AppBeginning from '../components/AppBeginning';
import AppReviews from '../components/AppReviews';
import AppUseful from '../components/AppUseful';
export default {
    mounted() {
        // window.scrollTo(0, 0);
        window.scrollBy({
            top: -Math.max( document.body.scrollHeight, document.body.offsetHeight, 
                document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight ),
            behavior: 'smooth'
        });
    },
    components: { AppHistory, AppAbout,
        AppServices, AppWhy, AppStages,
        AppExamples, AppBeginning, AppReviews,
        AppUseful}
}
</script>
    
