<template>
  <app-bredcrumbs></app-bredcrumbs>
  <app-main-services></app-main-services>
  <app-beginning></app-beginning>
</template>

<script>
import AppBredcrumbs from '../components/AppBredcrumbs';
import AppMainServices from '../components/AppMainServices';
import AppBeginning from '../components/AppBeginning';
export default {
  mounted() {
    // window.scrollTo(0, 0);
    this.$store.commit('changePage', {value : 0});
    window.scrollBy({
        top: -Math.max( document.body.scrollHeight, document.body.offsetHeight, 
            document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight ),
        behavior: 'smooth'
    });
  },
  components: { AppBredcrumbs, AppMainServices, AppBeginning}
}
</script>

