import { createRouter} from 'vue-router'
import { createWebHistory} from 'vue-router'
import MainPage from './views/MainPage';
import ServicesMain from './views/ServicesMain';
import ServicesBooks from './views/ServicesBooks';
import ServicesGenResearch from './views/ServicesGenealogResearch';
import ServicesFamilyTrees from './views/ServicesFamilyTrees';
import ServicesMilitarySearch from './views/ServicesMilitarySearch';
import KnowledgeBase from './views/KnowledgeBase';
import NotFound from './views/NotFound';

const router = createRouter({
    base: '/home',
    history: createWebHistory(),  // Исторя для прехода по кнопке назад
    routes: [
        { path: '/home', component: MainPage, alias: '/', name: 'home'},   // localhost:port/home  ***** alias: '/'
        { path: '/services', component: ServicesMain, name: 'services'}, 
        { path: '/services/books', component: ServicesBooks, name: 'books'},
        { path: '/services/family_trees', component: ServicesFamilyTrees, name: 'family_trees'},
        { path: '/services/genealogical_research', component: ServicesGenResearch, name: 'genealogical_research'},
        { path: '/services/military_search', component: ServicesMilitarySearch, name: 'military_search'},
        { path: '/knowledge_base', component: KnowledgeBase, name: 'knowledge_base'},
        { path: '/:notFound(.*)', component: NotFound}
        //{ path: '/:notFound(.*)', component: NotFound}        
        //{ path: '/forget', component: AppForget, meta: {cantEnter: true}},
        // { path: '/mail/:mailId?', component: AppMail}, //символ вопроса означает необязательный параметр
        // { path: '/:notFound(.*)', redirect: '/login'}
        // { path: '/:notFound(.*)', redirect: '/home'}
    ],
    linkActiveClass: 'active',  // по умолчанию 'router-link-active'
    linkExactActiveClass: 'active'  // по умолчанию 'router-link-exact-active'
})


