<template>
  <app-bredcrumbs></app-bredcrumbs>
  <app-military-search></app-military-search>
</template>
  
  <script>
import AppBredcrumbs from "../components/AppBredcrumbs";
import AppMilitarySearch from "../components/AppMilitarySearch";
export default {
  mounted() {
    // window.scrollTo(0, 0);
    window.scrollBy({
      top: -Math.max(
        document.body.scrollHeight,
        document.body.offsetHeight,
        document.documentElement.clientHeight,
        document.documentElement.scrollHeight,
        document.documentElement.offsetHeight
      ),
      behavior: "smooth",
    });
    this.$store.commit("showBredcrumb");
    this.$store.commit("changeBredcrumb", { value: 3 });
    this.$store.commit('changePage', {value : 0})
  },
  unmounted() {
    this.$store.commit("hideBredcrumb");
  },
  components: { AppBredcrumbs, AppMilitarySearch },
};
</script>
  
