export default {
    state() {
        return {
            firstName: 'Alex',
            lastName: 'Peterson',
            phone: '+1 123 456 78 90',
            email: 'alex@gmail.com',
        }
    },
    getters: {
    },
    mutations: {
        // Мутации не должны быть асинхронными
    },
    actions: {
        // это мутации для асинхронного кода
        async createCallAsync(context) {
            const response = await fetch('https://bloodline-db-default-rtdb.europe-west1.firebasedatabase.app/people.json', {
                method: 'POST',
                headers: {
                'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    firstName: context.state.firstName,
                    lastName: context.state.lastName,
                    phone: context.state.phone,
                    email: context.state.email,
                })
            })
            const firebaseData = await response.json()
            console.log("Отправлен запрос", firebaseData)
        }
    }
}