export default {
    state() {
        return {
            isShow: false,
            bredcrumbs: ["Родословные древа", "Родословные книги", "Генеалогическое исследование", "Военный поиск", "Дизайн фамильного древа", "Разработка герба"],
            pages: ["Услуги", "База знаний"],
            currentNum: 0,
            currentPageNum: 0,
        }
    },
    getters: {
        currentBredcrumb(state) {   
            return state.bredcrumbs[state.currentNum]; 
        },
        currentPage(state) {   
            return state.pages[state.currentPageNum]; 
        },
        getPath(state) {
            let path = '/home';
			switch (state.currentPageNum) {
				case 0 : path = '/services/'
				break;
				case 1 : path = '/knowledge_base'
				break;
				default: path = '/home'
			}
            return path;
		}
        // doubleCounter(_, getters) {
        //     // нижнее подчеркивание означает что state не передается
        //     // стандартные входные параметры: (state, getters)
        //     return getters.counter * 2
        // }
    },
    mutations: {
        // Мутации не должны быть асинхронными
        showBredcrumb(state) {
            state.isShow = true;
        },
        hideBredcrumb(state) {
            state.isShow = false;
        },
        changeBredcrumb(state, num) {
            state.currentNum = num.value;
        },
        changePage(state, num) {
            state.currentPageNum = num.value;
        },
    },
    actions: {
        // это мутации для асинхронного кода

    }
}