                <div class="examples__title">
                    Примеры работ
                </div>
                <div class="examples__slider">
                    <button type="button" class="slider_prev-btn prev disabled"></button>
                    <div class="slider">                        
                        <div class="slider-list">
                          <div class="slider-track">                        
                            <div class="slide">
                                <img src="../assets/img/slide_1.png" alt="examples-image-1" class="examples-image">
                            </div>
                            <div class="slide">
                                <img src="../assets/img/slide_1.png" alt="examples-image-2" class="examples-image">
                            </div>
                            <div class="slide">
                                <img src="../assets/img/slide_1.png" alt="examples-image-3" class="examples-image">
                            </div>
                            <div class="slide">
                                <img src="../assets/img/slide_1.png" alt="examples-image-4" class="examples-image">
                            </div>
                            <div class="slide">
                                <img src="../assets/img/slide_1.png" alt="examples-image-5" class="examples-image">
                            </div>
                          </div>
                        </div>                        
                    </div>
                    <button type="button" class="slider_next-btn next"></button>
                </div>
                <div class="examples__text">
                    Родословное древо (VIP-пакет)
                </div>
                <button class="examples__btn">
