                </button>
            </div>
        </div>
    </section>
</template>

<script>
import {dragDropSlider} from '../assets/js/slider.js'
export default {
    mounted() {
        try {
            dragDropSlider('.slider', '.slider-list', '.slider-track', '.slide', '.examples__slider', 5);
        } catch (error) {console.log(error)}
    }
}
</script>

<style>
.expamples{
