	border-radius: 30%;	
	background-repeat: no-repeat;
	background-size: 120% 120%;
	background-position: center center;
	width: 60px;
	height: 60px;
}
.slider_prev-btn{
	background-image: url("../assets/img/btn_slider_left.svg");
}
.slider_next-btn{
