	}
	.examples__slider{
		margin-top: 50px;
	}
	.slide, .slider-list, .slider{
		max-width: 320px;
		max-height: 216px;
	}
	.examples__text{
		margin-top: 35px;
		margin-bottom: 24px;
		font-size: 18px;
		line-height: 22px;
	}
	.examples__btn{
		font-size: 16px;
		line-height: 22px;
	}
}
</style>